import Vue from 'vue';
import Vuex from 'vuex';
import countryPointsJson from '@/util/countryPoints.json';
import ui from './ui';
import abi from '@/util/abi.json'

const contractAddress = "0xEd49879DDa59E45A2433b36Cfe0931DC26E964Ea"

Vue.use(Vuex);





export default new Vuex.Store({
  modules: {
    ui,
  },
  state: {
    tronWeb: {
      installed: false,
      loggedIn: false
    },
    landArr: [],
    nowGlobal: null,
    address: ''
  },
  mutations: {
    setTronWeb(state, tronWebInfo) {
      state.tronWeb = tronWebInfo;
    },
    setAddress(state, address) {
      state.address = address;
    },
    setLandArr(state, landArr) {
      state.landArr = landArr
      state.landInfoUpdateAt = new Date();
    },
    setNowGlobal(state, nowGlobal) {
      state.nowGlobal = nowGlobal
    },
  },
  actions: {
    async getLangArr ({ commit }) {
      let result = null
      const allOfABI = connex.thor.account(contractAddress).method(abi["allOf"])
      const promises = [...Array(220)].map((item, index) => {
        return allOfABI.call(index + 1)
      })
      Promise.all(promises).then(resp => {
        const langarr = []
        console.log(resp.length, countryPointsJson);
        for (let i = 0; i < resp.length - 1; i++) {
          langarr.push([
            resp[i].decoded[0],
            resp[i].decoded[2] / (10 ** 18),
            resp[i].decoded[1],
            countryPointsJson[i].code
          ])
        }
        commit('setLandArr', langarr)
      })


      connex.thor.account(contractAddress).method(abi["getNowGlobal"]).call().then(resp => {
        commit('setNowGlobal', [resp.decoded._end , resp.decoded._lastone, (resp.decoded._pool / 10 ** 18).toFixed(2)])
      })
    },
    async getAddress ({ commit }) {
      // userInfoaddress = await client.api.asset.getAccount()
      commit('setAddress', 'amzamzamz')
    }
  },
});
